.taptap {
    position: relative;
    overflow: hidden;
    img {
        height: 0.9375rem;
        animation: taptap_scroll 0.5s infinite linear;
        animation-play-state: paused;
    }

    .cover,.block {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        animation: taptap_cover_reveal forwards linear;
    }

    .cover {
        background: white;
        z-index: 1;
    }

    .block {
        background: transparent;
        z-index: 3;
    }

    .tap {
        width: 16vw;
        height: 16vw;
        font-size: 0.16rem;
        line-height: 16vw;
        border-radius: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        margin-left: -8vw;
        margin-top: -8vw;
        text-align: center;
        z-index: 2;
        background: white;
        box-shadow: 0.05rem 0.05rem 0.1rem #bfbdbd;

        &:active {
            color: white;
            background: black;
            box-shadow: none;
        }
    }
}

@keyframes taptap_scroll {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-0.9375rem);
    }
}

@keyframes taptap_cover_reveal {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-100%);
    }
}
